h1 {
  font-family: Poppins, sans-serif;
  font-size: 45px;
  font-weight: 600;
  letter-spacing: 4.5px;
  line-height: 67.5px;
}

h4 {
  font-family: Poppins, sans-serif;
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 2.5px;
  line-height: 36px;
}

h5 {
  font-family: Poppins, sans-serif;
  font-size: 20px;
  font-weight: 300;
  letter-spacing: 2px;
  line-height: 26px;
}

h6 {
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1.6px;
  line-height: 22px;
}

p,
.nav-link {
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 26px;
}

strong {
  font-weight: bold;
}

/* Mobile and tablet specific styling */
@media all and (max-width: 890px) {
  h1 {
    font-family: Poppins, sans-serif;
    font-size: 25px;
    font-weight: 600;
    letter-spacing: 2.5px;
    line-height: 30px;
  }

  h4 {
    font-family: Poppins, sans-serif;
    font-size: 23px;
    font-weight: 600;
    letter-spacing: 2.3px;
    line-height: 36px;
  }

  h5 {
    font-family: Poppins, sans-serif;
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 1.8px;
    line-height: 26px;
  }

  h6 {
    font-family: Poppins, sans-serif;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1.6px;
    line-height: 22px;
  }

  p,
  .nav-link {
    font-family: Poppins, sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 26px;
  }
}
